<template>
	<view class="content">
		<div class="mrow">
			<input v-model="name" />
			<!-- <button @click="search">查询</button> -->
			<!-- <div>{{ info }}</div> -->
			<!-- <button @click="searchmatches">查询</button> -->
			<button @click="init">查询</button>
		</div>
		<div class="mrow">
			<div class="mcol">
				猜对: {{right}}
				猜错: {{wrong}}
			</div>
			<div class="mcol" style="margin-left: 2em;"><div>时长: {{ during }}</div></div>
			<div class="mcol"></div>
		</div>
		
		<div class="mrow start">
			<!-- 			<div class="mcol" style="">
				<div class="mrow" :key="item.gameId" v-for="(item, index) in data.matches" @click="current=index">
				<div v-show="current==index" style="font-size: 40rpx;">·</div>
				<div>{{ getName(item.champion) + item.gameId }}</div></div>
			</div> -->
			<div class="mcol" style="">
				<div class="mrow">
					<button class="mcol" style="50rpx" @click="check(100)">left wins</button>
					<div class="mcol" style="width:250rpx">
						<div class="mcol" v-for="(item, index) in team100" :key="item.id" @click="check(100, index)">
							<img style="width:150rpx" mode="widthFix" :src="getImg(item.championId)" alt="" />
							<div style="font-size:12px">{{ getName(item.championId) }}</div>
							<!-- {{item.stats.win}} -->
						</div>
					</div>
					<div class="mcol" style="width:50rpx">VS</div>
					<div class="mcol" style="width:250rpx">
						<div class="mcol" v-for="(item, index) in team200" :key="item.id" @click="check(200, index)">
							<img style="width:150rpx" mode="widthFix" :src="getImg(item.championId)" alt="" />
							<div style="font-size:12px">{{ getName(item.championId) }}</div>
							<!-- {{item.stats.win}} -->
						</div>
					</div>
					<button class="mcol" style="50rpx" @click="check(200)">right wins</button>
				</div>
				<div class="mrow">
					<button :disabled="!current" @click="current--">上一题</button>
					<button @click="next" style="margin:0 10rpx">随机</button>
					<button :disabled="current >= matchlength" @click="current++">下一题</button>
				</div>
				<!-- <div class="mrow"> -->
				<!-- <button :disabled="!current" @click="current--">上一题</button> -->

				<!-- </div> -->
			</div>
		</div>

		<!-- 		<div v-for="(item,index) in lol.data" :key="item.key">
			{{ item.key+':'+item.name }}
		</div> -->
	</view>
</template>

<script>
import loljson from '@/utils/lol.json';
export default {
	data() {
		let lol = loljson;
		let temp = lol.data;
		lol.data = [];
		for (let id in temp) {
			lol.data.push(temp[id]);
		}
		lol.data = lol.data.sort((a, b) => a.key - b.key);
		return {
			name: 'hideonbush',
			info: {},
			data: { matches: [] },
			lol,
			detaildata: { participants: [], gameDuration: 0 },
			current: 0,
			gameId: '',
			pregameid: '',
			right:0,
			wrong:0
		};
	},
	computed: {
		matchlength() {
			return this.data.matches.length;
		},
		team100() {
			let { detaildata } = this;
			return detaildata.participants.filter(one => one.teamId == 100);
		},
		team200() {
			let { detaildata } = this;
			return detaildata.participants.filter(one => one.teamId == 200);
		},
		during() {
			let {
				detaildata: { gameDuration }
			} = this;
			let min = Math.floor(gameDuration / 60);
			let sec = gameDuration % 60;
			let res = (min > 9 ? min : '0' + min) + ':' + (sec > 9 ? sec : '0' + sec);
			return res;
		}
	},
	watch: {
		current(val) {
			this.autodetail();
		},
		gameId(gameId) {
			this.detailbygameid(gameId);
		}
	},
	async onLoad() {
		// await thi|s.getlol()
		let {storage} = this
		let games = storage.get('games',[])||[]
		this.right = games.filter(g=>g.result).length
		this.wrong = games.filter(g=>!g.result).length
		let name = storage.get('tempname');
		if (name) {
			this.name = name;
		}
		await this.init();
	},

	methods: {
		next() {
			this.pregameid = this.gameId;
			this.gameId = 4660000000 + Math.round(Math.random() * 10000000);
			this.data.matches = [];
			this.current = 0
		},
		async init() {
			await this.search();
			await this.searchmatches();
			await this.autodetail();
		},
		async autodetail() {
			let {
				data: { matches },
				current,
				detail
			} = this;
			if(matches.length)
			this.gameId = matches[current].gameId;
			// detail(matches[current])
		},
		check(item, index) {
			let {
				detaildata: {
					teams: [{ win, teamId }]
				},
				storage
			} = this;
			let team = this['team' + item];
			let flag = !((win == 'Win') ^ (item == teamId))
			if (index != undefined) {
				if (item == 200) index += 5;
				storage.set('lastname', this.name)
				this.name = this.detaildata.participantIdentities[index].player.summonerName;
				storage.set('tempname', this.name);
			} else {
					let games = this.storage.get('games',[])||[]
					let marked = games.find(g=>{
						return g.gameId == this.gameId
					})
					if(!marked){
					games.push({
						gameId: this.gameId,
						result: flag
					})
					if(flag){
						this.right++
					}else{
						this.wrong++
					}
					storage.set('games',games)
					}else{
						
					}
				if (flag) {

					uni.showToast({
						icon: 'success',
						title: '猜对啦'
					});
				} else {
					uni.showToast({
						icon: 'none',
						title: '猜错啦'
					});
				}
			}
		},
		getName(key) {
			let { lol } = this;
			return (lol.data.find(one => one.key == key) || {}).name;
		},
		getImg(key) {
			let { lol } = this;
			return 'http://ddragon.leagueoflegends.com/cdn/10.19.1/img/champion/' + (lol.data.find(one => one.key == key) || {}).image.full;
		},
		async search() {
			let { get, name, storage } = this;
			await get(`/lol/summoner/v4/summoners/by-name/${name}`).then(res => {
				this.info = res;
				return Promise.resolve();
			}).catch(err=>{
				let lastname = storage.get('lastname','hideonbush')
				this.name = lastname
				return Promise.resolve()
			})
		},
		async searchmatches() {
			let { get, info, storage, data } = this;
			let res = await get(`/lol/match/v4/matchlists/by-account/${info.accountId}`, { queue: 420 });
			// this.data = res;
			let games = storage.get('games',[])||[]
			data.matches = res.matches.filter(one=>{
				let gflag = games.find(g=>{
					return g.gameId == one.gameId
				})
				return !gflag
			})
			// this.data.matches.length = this.data.matches.length;
			return Promise.resolve();
		},
		detail(item) {
			let { get } = this;
			get(`/lol/match/v4/matches/${item.gameId}`).then(res => {
				this.detaildata = res;
				return Promise.resolve();
			});
		},
		detailbygameid(gameId) {
			let { get } = this;
			uni.showLoading();
			get(`/lol/match/v4/matches/${gameId}`)
				.then(res => {
					// let flag = res.gameMode == 'CLASSIC'&&res.gameType == 'MATCHED_GAME'
					let flag = res.queueId == 420;
					if (flag) {
						this.detaildata = res;
						uni.hideLoading();
					} else {
						this.next();
					}
					return Promise.resolve();
				})
				.catch(err => {
					this.next();
					return Promise.resolve();
				});
		}
	}
};
</script>

<style lang="scss">
.content {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.logo {
	height: 200rpx;
	width: 200rpx;
	margin-top: 200rpx;
	margin-left: auto;
	margin-right: auto;
	margin-bottom: 50rpx;
}

.text-area {
	display: flex;
	justify-content: center;
}

.title {
	font-size: 36rpx;
	color: #8f8f94;
}
</style>
